.stack {
  position: relative;
  width: 340px;
  height: 410px;
  overflow: visible;
}
.card-anim {
  width: 300px;
  height: 350px;
  background: linear-gradient(
    70.8deg,
    rgb(31, 42, 147) 92.4% rgb(253, 70, 90) 24%
  );
  position: absolute;
  animation: onTop 1.5s 1s ease infinite;
}
.card-ani.one {
  left: 0;
  top: 0;
  z-index: 2;
  animation-name: onTop;
}
.card-anim.two {
  left: 20px;
  top: 20px;
  z-index: 1;
  animation-name: onMiddle;
}
.card-anim.three {
  left: 40px;
  top: 40px;
  z-index: 0;
  animation-name: onBottom;
}
.card-anim span {
  font-size: 24px;
  font-family: "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans",
    "DejaVu Sans", Verdana, "sans-serif";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
}
@keyframes onTop {
  5% {
    transform: translateY(-300px);
    z-index: 1;
    left: 20px;
    top: 20px;
  }
  10% {
    transform: translateY(0);
    z-index: 0;
    left: 40px;
    top: 40px;
  }
  40% {
    transform: translateY(0);
    z-index: 0;
    left: 40px;
    top: 40px;
  }
  50% {
    transform: translateY(0);
    z-index: 1;
    left: 20px;
    top: 20px;
  }
  70% {
    transform: translateY(0);
    z-index: 1;
    left: 20px;
    top: 20px;
  }
  80% {
    transform: translateY(0);
    z-index: 2;
    left: 0;
    top: 0;
  }
}
@keyframes onMiddle {
  10% {
    transform: translateY(0);
    z-index: 2;
    left: 0;
    top: 0;
  }
  40% {
    transform: translateY(0);
    z-index: 2;
    left: 0;
    top: 0;
  }
  45% {
    transform: translateY(-300px);
    z-index: 1;
    left: 20px;
    top: 20px;
  }
  50% {
    transform: translateY(0);
    z-index: 0;
    left: 40px;
    top: 40px;
  }
  70% {
    transform: translateY(0);
    z-index: 0;
    left: 40px;
    top: 40px;
  }
  80% {
    transform: translateY(0);
    z-index: 1;
    left: 20px;
    top: 20px;
  }
}
@keyframes onBottom {
  10% {
    transform: translateY(0);
    z-index: 0;
    left: 20px;
    top: 20px;
  }
  40% {
    transform: translateY(0);
    z-index: 1;
    left: 20px;
    top: 20px;
  }
  50% {
    transform: translateY(0);
    z-index: 2;
    left: 0;
    top: 0;
  }
  70% {
    transform: translateY(0);
    z-index: 2;
    left: 0;
    top: 0;
  }
  75% {
    transform: translateY(-300px);
    z-index: 1;
    left: 20px;
    top: 20px;
  }
  80% {
    transform: translateY(0);
    z-index: 0;
    left: 40px;
    top: 40px;
  }
}
